.text-box {
  padding: 10px; /* Adds space inside the input */
  font-size: 16px; /* Adjust font size if needed */
  width: 160px;
  height: 42px; /* Optional: make the input take full width of the container */
  box-sizing: border-box; /* Ensures padding is included in the width and height */
}

.text-box::placeholder {
  color: #999; /* Change placeholder text color */
  /* Change placeholder text style */
}

/* Optional additional styling for the input */
.text-box {
  border: 1px solid #ccc; /* Border style */
