}

.select-box {
  padding: 10px; /* Adds space inside the select box */
  font-size: 16px; /* Adjust font size if needed */
  width: 100%; /* Make the select box take the full width of the container */
  box-sizing: border-box; /* Ensures padding is included in the width and height */
  border: 1px solid #ccc; /* Border style */
  border-radius: 4px; /* Rounded corners */
}

.text-box-mobile {
  padding: 10px; /* Adds space inside the select box */
  font-size: 16px; /* Adjust font size if needed */
  width: 100%; /* Make the select box take the full width of the container */
  box-sizing: border-box; /* Ensures padding is included in the width and height */
  border: 1px solid #ccc; /* Border style */
  border-radius: 4px; /* Rounded corners */
}
.text-box-mobile {
  -webkit-appearance: none; /* Chrome, Safari, Opera */
  -moz-appearance: textfield; /* Firefox */
  appearance: none; /* Standard property */
}
.text-box-mobile::-webkit-outer-spin-button,
.text-box-mobile::-webkit-inner-spin-button {
  -webkit-appearance: none; /* For Chrome, Safari, Edge, Opera to hide arrows */
  margin: 0; /* Remove default margin */
}

