.grid img {
  max-width: 100%;
  max-height: 100%;
}

.Campus-img {
  width: 1274px;
  height: 542px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 300px); /* Set a fixed height for the rows */
  /* Removes gap between grid items */
}

.Campus-img div {
  overflow: hidden; /* Ensures the images do not overflow their container */
}

.Campus-img div img {
  width: 100%; /* Ensures images take full width of their container */
  height: 100%; /* Ensures images take full height of their container */
  object-fit: cover; /* Ensures images cover the container while maintaining aspect ratio */
  border-radius: 10px;
}

